body{
    position: relative;
    font-family: 'Arial', 'Helvetica Neue', Helvetica, sans-serif;
    
  
}
 
#heading{
    font-family:  'Georgia', Times, 'Times New Roman', 'serif';
    background:#556270; 
}

 
.main-header{
    background:  #556270;
    border: 1px solid #cccccc;
    margin-top: 20px;
    padding-right: 20px;
    text-align: center;
    width: 370px;
    margin-bottom: 0px;
    color: white;
    border-radius: 3px;
    border: solid 1px #556270;
    border-radius: 20px;
    
}


#link_wrapper{
    float: right;
    color:#556270;
    text-align: center;
    margin: 20px;

}

.a_link{
    color: #ffffff;
    font-size: 18px;
    text-decoration: none;
}
.box-set{
    box-sizing: border-box;
    width: 100%;
    border: 2px solid #dddddd;
    padding-bottom: 15px;
}
 
.mainC{
    clear: both;
    border: 2px  #dddddd;
    background: #ffffff;
    margin: 0 auto; 
    width: 700px;   
    padding-bottom: 0px; 
    margin-top: 30px;
    margin-bottom: 20px;
    box-sizing: border-box;
    border: solid 1px #556270 ;
    border-radius: 15px;
}

#sub1_text{
    color:#556270;
    margin: 20px;
    padding: 10px;
    padding-left: 0;
    font-family:  'Georgia', Times, 'Times New Roman', 'serif';
}

form{
    clear: both;
    border: 1px  #dddddd;
    background: #ffffff;
    margin: 0 auto;   
    margin-right: 30px;
}
 
#line{
    border-bottom: 2px solid #dddddd;
}

#fields{
    margin-left: 20px;
    margin-top: 15px;
    color: #777777;
    font-weight: normal;
    
}

input[type=text]{

    width: 89%; 
    padding: 10px 5px;
    margin-bottom: 10px;
    border-radius: 3px;
    border: 1.2px solid #556270;
     
}

textarea{
    width: 90%;
    height: 150px;
    border-radius: 3px;
    overflow-wrap: break-word;
    border: 1.2px solid #556270;
    box-sizing: border-box;
    resize: none;
   
}
input[type=submit]{
    padding: 15px 40px;
     
    margin-top: 20px;
    margin-bottom: 50px;
    margin-right: 0px;
    background: #556270;
    color: white;
    border: 2.2px solid #556270;
    border-radius: 5px;
    text-align: center;
    font-size: large;
    font-weight: bold;
    cursor: pointer;
}
 .ppp{   
    font-weight: 50px;
    color: #777777;
    line-height: 250%;
    margin-top: 10px;
    padding-top: 10px;
}
.icon{

    background:#dddddd;
    margin-top: 10px;
}
#plink_wrapper{
    margin: auto;
    padding-bottom: 20px;
    color: #556270;
   
   }

@media only screen 
and (min-width: 769px)
and (max-width: 980px){
    #link_wrapper{
        float: right;  
        word-spacing: 15px;
        font-size: 18px; 
        
    }
    
    .mainC{
        width: 790px;
    }

}



@media only screen 
and (min-width: 641px)
and (max-width: 768px){
    .all_images{
        margin: 0 auto;
        left: 70px;
    }
    
         .mainC{
             width: 600px;
         }
}
 

    @media screen and (min-width:320px) and (max-width:640px) {
        .main-header{
            width: 100%;
            text-align:center;
            
            
        }
        #link_wrapper {
            width: 100%;
            left: 0px;
            text-align: center;
            word-spacing: 15px;
            font-size: 18px; 
            margin-top: -30px;   
        }
       
             
         
         .P-img{
             display: block;
             float: none;
             margin: 10px !important; 
             left: 30px;
             
         }
         .mainC{
             width: 420px;
         }


    }